require('styles/Nav.css');
import React from 'react';
import { Navbar, Nav, NavItem} from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';

class NavComponent extends React.Component {
  render() {
    return (
        <Navbar inverse fixedTop>
          <Navbar.Header>
            <Navbar.Brand>
              <a href="#">_</a>
            </Navbar.Brand>
            <Navbar.Toggle />
          </Navbar.Header>
          <Navbar.Collapse>
            <Nav pullRight>
              <LinkContainer to='/technologyList'>
                <NavItem eventKey={1} href="#"></NavItem>
              </LinkContainer>
              <LinkContainer to='/record'>
                <NavItem eventKey={2}></NavItem>
              </LinkContainer>
              <NavItem eventKey={3} href="#"></NavItem>
              <LinkContainer to='/hiChat'>
                <NavItem eventKey={4} href="#"></NavItem>
              </LinkContainer>
              <NavItem eventKey={5} href="#"></NavItem>
            </Nav>
          </Navbar.Collapse>
          
        </Navbar>
    );
  }
}

NavComponent.defaultProps = {
};

export default NavComponent;


